'use client';

import React from 'react';
import Link from 'next/link';

import header from '@/../components/header'

export default function AuthLayout({ children }: { children: React.ReactNode; }) {
    
    return (
        <>
            
            <header className="bg-ccGray text-white py-4 px-6 shadow-md">
                <div className="h-12 container mx-auto flex items-center justify-between">
                    
                    <Link href="/" className="flex items-center gap-2">
                        <h1 className="text-xl font-bold">Cine Conecta</h1>
                    </Link>
                    
                    <nav>
                        <ul className="flex space-x-6">

                            <li>
                                <Link href="register" className="hover:text-slate-300 transition">
                                    Inicio
                                </Link>
                            </li>

                            {/* <li>
                                <Link href="register" className="hover:text-slate-300 transition">
                                    Pel√≠culas
                                </Link>
                            </li>
                            <li>
                                <Link href="register" className="hover:text-slate-300 transition">
                                    Recomendaciones
                                </Link>
                            </li>
                            <li>
                                <Link href="register" className="hover:text-slate-300 transition">
                                    Perfil
                                </Link>
                            </li> */}
                        </ul>
                    </nav>
                </div>
            </header>

            <main className=" py-6 px-4 bg-ccDarkGray">
                {children}
            </main>
            
        </>
    );
}